<template>
  <div class="showContent checkInfoContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <backSubMenu :backTitle="backTitle" :lineUnder="false"></backSubMenu>
    <div class="detail">
      <div class="left">
        <div class="infor_form_detail">
          <div>
            <!-- 公司名 -->
            <label>Name</label>
            <input type="text" :value="detailData.name" readonly>
          </div>
          <div>
            <!-- ID -->
            <label>ID Number</label>
            <input type="text" :value="detailData.idNumber" readonly>
          </div>
          <div>
            <!-- Province -->
            <label>Province</label>
            <input type="text" :value="detailData.province" readonly>
          </div>
          <div>
            <!-- city -->
            <label>City</label>
            <input type="text" :value="detailData.city" readonly>
          </div>
          <div>
            <!-- district -->
            <label>District</label>
            <input type="text" :value="detailData.district" readonly>
          </div>
          <div>
            <!-- village -->
            <label>Village</label>
            <input type="text" :value="detailData.village" readonly>
          </div>
          <div>
            <!-- village -->
            <label>Name Similar</label>
            <input type="text" :value="detailData.namePercent" readonly>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="infor_form_detail">
          <div>
            <!-- Time -->
            <label>Time</label>
            <input type="text" :value="$util.timestampToDay(record.created_at)" readonly>
          </div>
          <!-- <div>
            <label>Event</label>
            <input type="text" :value="detailData.name" readonly>
          </div> -->
          <div class="hasDot">
            <!-- Situation -->
            <label>Situation</label>
            <input type="text" :value="record.status" readonly>
            <span :class="record.status=='success'?'greenDot':'redDot'" id="statusDot">
              <i class="iconfont amd-dot"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import backSubMenu from "@/components/backSubMenu";
import { mapGetters } from "vuex";
import { fail } from "assert";

export default {
  data() {
    return {
      routekey: [
        "checkPersonInfo",
        "personInfoHistory;personInfoHistoryDetail"
      ],
      backTitle: "Details",
      detailData: "",
      record:"",
    };
  },
  computed: {},
  components: {
    requestAndHisSubMenu,
    backSubMenu
  },
  methods: {
    getDetailData() {
      let params = this.$route.params;
      console.log(params);
      this.record=params.record;
      this.detailData = params.record.response.data;
      console.log(this.detailData);
    }
  },
  created() {
    if(this.$route.params.record){
      this.getDetailData();
    }else{
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less">
@import "../../../css/checkInfor.less";
</style>





